import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";
import { Tabs, TabList, Tab, TabPanels, TabPanel } from 'carbon-components-react';

const dispatchedEvents = ["ABOUT#clicked"];

/*
    Example: <srs-tab-set>
                <srs-tab-item title="Item 1">
                    <p>Content of Item 1</p>
                </srs-tab-item>
                <srs-tab-item title="Item 2">
                    <p>Content of Item 2</p>
                </srs-tab-item>
            </srs-tab-set>
*/
const SrsTabSet = function (props, ref) {
    const [activeTab, setActiveTab] = useState(0);
    return (
        <div>
            <Tabs type="container" selectedIndex={activeTab} 
                onChange={index => {
                    setActiveTab(index?.selectedIndex || 0)
                }}>
                <TabList aria-label="Tab List">
                    {props.items.map((tabItem, index) => {
                        console.log(tabItem);
                        return (
                            <Tab key={index}>{tabItem.title}</Tab>
                        )
                    })}
                </TabList>
                <TabPanels>
                    {props.items.map((tabItem, index) => (
                        <TabPanel key={index}>
                            {activeTab === index? <slot name={tabItem.slotname}/>: null}
                        </TabPanel>
                    ))}
                </TabPanels>
            </Tabs>
        </div>
    );
}

const styles = `
    
`
const props = [{
    name: "items",
    defaultValue: [],
    type: "object"
}]

const slots = [{name:"$DYNAMIC$", script: `(function(props) {
    let slots = [];
    if(props.items) {
        props.items.map((item) => {
            slots.push({name: item.slotname, allowed: ["srs-tab-item"]});
        })
    }
    return slots;
})`}];

const WrappedComponent = wrapper(React.forwardRef(SrsTabSet), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };